
























import { Component, Prop, Vue } from 'vue-property-decorator'
import lightbox from 'vue-easy-lightbox'

@Component({
  components: {
    lightbox
  }
})
export default class Gallery extends Vue {
  visible = false
  index = 0

  @Prop() private msg!: string;

  get images (): string[] {
    const imagesList = require.context(
      '@/assets',
      true,
      /^.*\.jpg$/
    )
    const images = imagesList.keys().map(e => imagesList(e))
    return images
  }

  get thumbnails (): string[] {
    const imagesList = require.context(
      '@/assets/thumbnail',
      true,
      /^.*\.jpg$/
    )
    const images = imagesList.keys().map(e => imagesList(e))
    return images
  }

  showImg (index: number): void {
    this.index = index
    this.visible = true
  }

  handleHide (): void {
    this.visible = false
  }
}
